<template>
    <div id="adjuntos">
      <v-card
        class="mx-auto"
        dense
        v-for="item in dataAdjuntos" :key="item.tipo"
      >
        <v-card-text>
            <span style="font-size: 20px;">Haz click en el nombre del archivo para descargarlo</span><br><br>
            <v-chip v-if="item.estado == 'A'"
                class="ma-2"
                @click="openInNewTab(item.file_path, item.file_type)"
                color="green darken-2"
                text-color="white"
                style="margin: 5px"
            >
                {{item.file_name.substring(0, 50)}}
            </v-chip>
        </v-card-text>
      </v-card>
    </div>
  </template>
<script>
import { adminApp } from '@/services/api'
import FileExchange from '../../services/FileExchange'
import { b64toBlobUrl } from '../../services/utilities'
export default {
  props: ['idpropconv'],
  data () {
    return {
      dataAdjuntos: [],
      resetComponent: 0,
      fileReset: 0,
      cargardefinitivo: 0
    }
  },
  async mounted () {
    this.getData()
  },
  methods: {
    async getData () {
      this.dataAdjuntos = []
      const { data } = await adminApp.buscarMinutaContrato({
        idpropconv: this.idpropconv
      })
      this.dataAdjuntos = data.archivos.filter(function (e) {
        return e.tipo === 2
      })
    },
    async openInNewTab (url, type) {
      try {
        const result = await FileExchange.getFileFromStorage(url)
        const fileData = result.data
        const urlBlob = b64toBlobUrl(fileData, type, '')
        var win = window.open(urlBlob, '_blank')
        win.focus()
      } catch (error) {
        console.log(error)
      }
    }
  },
  computed: {
    adjuntos: {
      get () {
        return this.dataAdjuntos
      },
      set (val) {
        this.adjuntos = val
      }
    }
  },
  watch: {
    idpropconv (val) {
      this.getData()
    }
  }
}
</script>
<style>
.anexosClass {
    min-width: 300px;
}
#adjuntos .v-label {
    font-size: .70rem;
}
#adjuntos .v-label .inFiles{
    font-size: .95rem;
}
.ColorVBTrue{
    color:blue;
    font-weight: bold;
}
.ColorVBFalse{
    color:red;
    font-weight: bold;
}
